/*---------------- TYPOGRAPHY ----------------*/
@font-face {
  font-family: 'Lato';
  src: url("../fonts/LatoLatin-Regular.woff2") format("woff2"), url("../fonts/LatoLatin-Regular.woff") format("woff"); }

@font-face {
  font-family: 'Lato-Light';
  src: url("../fonts/LatoLatin-Light.woff2") format("woff2"); }

@font-face {
  font-family: 'Lato-Semibold';
  src: url("../fonts/LatoLatin-Semibold.woff2") format("woff2"), url("../fonts/LatoLatin-Semibold.woff") format("woff"); }

@font-face {
  font-family: 'Lato-Bold';
  src: url("../fonts/LatoLatin-Bold.woff2") format("woff2"), url("../fonts/LatoLatin-Bold.woff") format("woff"); }

/*---------------- BROWSER SIZING ----------------*/
.notice-header-bar {
  position: fixed;
  height: 70px;
  width: 100%;
  top: 0;
  -webkit-box-shadow: 0px 1px 1px 0 rgba(0, 0, 0, 0.15);
  box-shadow: 0px 1px 1px 0 rgba(0, 0, 0, 0.15);
  left: 0;
  background-color: white; }

.notice-container-outer {
  min-height: 100vh;
  height: 100%;
  padding: 70px 10px;
  display: -ms-flexbox;
  display: flex; }

.notice-container-inner {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: start;
  align-items: flex-start;
  padding: 10px 10px 10px 10px; }

.notice-centered-content {
  width: 100%;
  max-width: 650px;
  padding: 10px 0 0 0; }

.notice-image {
  width: 100%;
  height: 300px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  -o-object-fit: contain;
  object-fit: contain;
  border: 0; }

.notice-header {
  text-align: left;
  margin-bottom: 0;
  font-size: 25px;
  line-height: 1.1;
  color: #09112c;
  font-family: Lato-Bold, Helvetica, Arial, Lucida Grande, sans-serif; }

.notice-paragraph-text {
  font-size: 16px;
  line-height: 1.1;
  color: #09112c;
  font-family: Lato-Light, Helvetica, Arial, Lucida Grande, sans-serif; }
  .notice-paragraph-text-link {
    font-size: 16px !important;
    line-height: 1.1 !important;
    color: #09112c !important;
    font-family: Lato-Light, Helvetica, Arial, Lucida Grande, sans-serif !important;
    text-decoration: underline !important; }
  .notice-paragraph-text-bold {
    font-family: Lato-Bold, Helvetica, Arial, Lucida Grande, sans-serif; }

.notice-paragraph-section {
  margin-bottom: 15px; }

.notice-cta-container {
  padding: 20px 0; }

.notice-cta-button {
  padding: 8px 15px;
  background-color: #09112c;
  color: #ffffff !important;
  font-family: Lato-Bold, Helvetica, Arial, Lucida Grande, sans-serif;
  font-size: 13px;
  line-height: 1.2;
  border-radius: 5px; }



