/*---------------- COLORS ----------------*/
/*---------------- TYPOGRAPHY ----------------*/
@font-face {
  font-family: 'Lato';
  src: url("../fonts/LatoLatin-Regular.woff2") format("woff2"), url("../fonts/LatoLatin-Regular.woff") format("woff"); }

@font-face {
  font-family: 'Lato-Light';
  src: url("../fonts/LatoLatin-Light.woff2") format("woff2"); }

@font-face {
  font-family: 'Lato-Semibold';
  src: url("../fonts/LatoLatin-Semibold.woff2") format("woff2"), url("../fonts/LatoLatin-Semibold.woff") format("woff"); }

@font-face {
  font-family: 'Lato-Bold';
  src: url("../fonts/LatoLatin-Bold.woff2") format("woff2"), url("../fonts/LatoLatin-Bold.woff") format("woff"); }

/*---------------- BROWSER SIZING ----------------*/
.white-header .solid-q-icon {
  fill: white; }

.white-header a .nav-link-text, .white-header a:focus .nav-link-text, .white-header a:hover .nav-link-text {
  color: white; }

.white-header .bounce-nav-list-item-link {
  color: white; }

.full-header-pinned {
  position: absolute;
  top: 0;
  height: 79px;
  z-index: 40; }

.header-spacer {
  position: relative;
  max-width: 1280px;
  width: 100%;
  margin: 0 auto;
  margin-top: 10px;
  display: block;
  overflow: hidden; }

.solid-q-link {
  height: 100%;
  width: 100%; }

.solid-q-container {
  width: auto;
  height: 47px;
  float: left;
  padding-left: 15px;
  padding-right: 10px; }
  .solid-q-container.nav-link-text {
    width: 100%;
    height: 100%; }

.solid-q-icon {
  fill: #09112c;
  float: left;
  width: 130px;
  max-width: 100%;
  display: block;
  height: 40px; }

.nav-container {
  float: right; }

@media (min-width: 500px) {
  .header-spacer {
    padding-left: 20px;
    padding-right: 20px; } }

@media (max-width: 500px) {
  .header-spacer {
    margin-top: 15px; }
  .bounce-nav-list a .nav-link-text, .bounce-nav-list a:focus .nav-link-text, .bounce-nav-list a:hover .nav-link-text {
    font-size: 15px; }
  .bounce-nav-list .join-button {
    font-size: 15px; }
  .bounce-nav-list .bounce-nav-list-item {
    padding-left: 5px;
    padding-right: 5px; }
  .solid-q-container {
    width: 120px;
    height: 40px; }
  .solid-q-icon {
    height: 30px;
    width: 95px; } }

a .nav-link-text, a:focus .nav-link-text, a:hover .nav-link-text {
  font-size: 16px;
  line-height: 1.1;
  font-family: Lato-Bold, Helvetica, Arial, Lucida Grande, sans-serif;
  color: #09112c;
  bottom: 0px;
  padding: 0px;
  margin: 0px; }

.bounce-nav-list-item-link:after {
  display: block;
  -webkit-transition: all 0.28s cubic-bezier(0.19, 1, 0.22, 1);
  -o-transition: all 0.28s cubic-bezier(0.19, 1, 0.22, 1);
  transition: all 0.28s cubic-bezier(0.19, 1, 0.22, 1);
  content: "";
  height: 3px;
  width: 0;
  bottom: -5px;
  position: absolute;
  background-color: #64d7d7; }

a:-webkit-any-link {
  color: -webkit-link;
  text-decoration: none;
  cursor: pointer; }

.bounce-nav-list-item-link, .bounce-nav-list-item-link .nav-link-text {
  position: relative;
  -webkit-transition: all 0.28s cubic-bezier(0.19, 1, 0.22, 1);
  -o-transition: all 0.28s cubic-bezier(0.19, 1, 0.22, 1);
  transition: all 0.28s cubic-bezier(0.19, 1, 0.22, 1); }

.bounce-nav-list {
  list-style: none;
  display: inline-block; }

.bounce-nav-list-item {
  display: block;
  float: left;
  padding-right: 15px;
  padding-left: 10px;
  padding-top: 5px; }

.bounce-nav-list-item-link {
  display: block;
  padding-right: 1.5px;
  padding-top: 5px;
  padding-bottom: 5px;
  border-bottom: none;
  border: 0;
  border-color: transparent;
  color: #09112c; }
  .bounce-nav-list-item-link:after {
    display: block;
    -webkit-transition: all 0.28s cubic-bezier(0.19, 1, 0.22, 1);
    -o-transition: all 0.28s cubic-bezier(0.19, 1, 0.22, 1);
    transition: all 0.28s cubic-bezier(0.19, 1, 0.22, 1);
    content: "";
    height: 3px;
    width: 0;
    bottom: 0px;
    position: absolute;
    background-color: #00F1FF;
    background: -webkit-gradient(linear, left top, right top, from(#a001FF), color-stop(50%, #00F1FF));
    background: -webkit-linear-gradient(left, #a001FF, #00F1FF 50%);
    background: -o-linear-gradient(left, #a001FF, #00F1FF 50%);
    background: linear-gradient(to right, #a001FF, #00F1FF 50%); }
  .bounce-nav-list-item-link:focus:after {
    width: 100%; }
  .bounce-nav-list-item-link:hover:after {
    width: 100%; }

a:hover {
  outline-width: 0; }

.bounce-nav-list-item-link:active .nav-link-text .nav-link-text, .bounce-nav-list-item-link:focus .nav-link-text .nav-link-text, .bounce-nav-list-item-link:hover .nav-link-text {
  bottom: 5px;
  padding-bottom: 10px; }
  .bounce-nav-list-item-link:active .nav-link-text .nav-link-text.solid-q-container, .bounce-nav-list-item-link:focus .nav-link-text .nav-link-text.solid-q-container, .bounce-nav-list-item-link:hover .nav-link-text.solid-q-container {
    bottom: 0;
    padding-bottom: 0; }

.bounce-nav-list-item-link .nav-link-text {
  bottom: 0;
  padding: 0; }

.bounce-nav-list-item-link, .bounce-nav-list-item-link .nav-link-text {
  position: relative;
  -webkit-transition: all 0.28s cubic-bezier(0.19, 1, 0.22, 1);
  -o-transition: all 0.28s cubic-bezier(0.19, 1, 0.22, 1);
  transition: all 0.28s cubic-bezier(0.19, 1, 0.22, 1); }



