/*---------------- COLORS ----------------*/
/*---------------- TYPOGRAPHY ----------------*/
@font-face {
  font-family: 'Lato';
  src: url("../fonts/LatoLatin-Regular.woff2") format("woff2"), url("../fonts/LatoLatin-Regular.woff") format("woff"); }

@font-face {
  font-family: 'Lato-Light';
  src: url("../fonts/LatoLatin-Light.woff2") format("woff2"); }

@font-face {
  font-family: 'Lato-Semibold';
  src: url("../fonts/LatoLatin-Semibold.woff2") format("woff2"), url("../fonts/LatoLatin-Semibold.woff") format("woff"); }

@font-face {
  font-family: 'Lato-Bold';
  src: url("../fonts/LatoLatin-Bold.woff2") format("woff2"), url("../fonts/LatoLatin-Bold.woff") format("woff"); }

/*---------------- BROWSER SIZING ----------------*/
body {
  margin: 0; }

h1, h2, h3, p {
  margin: 0; }

div {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }



